import clone from '@/lib/clone';
import createId from '@/lib/idCreate';
import TagHelper from '@/lib/mixins/TagHelper';
import router from '@/router';
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)//store vue.prototype.$store= store

// type RootState = {
//   recordList: RecordItem[],
//   tagList: Tag[],
//   currentTag?: Tag,
// };//custom2/23
const localStorageKeyName = 'recordList'
const store =  new Vuex.Store({
  state: {//data
    count: 0,
    // createRecordError:null,
    recordList:[] as RecordItem[],
    tagList: [] as Tag[],
    currentTag: undefined,
  } as RootState,
  mutations: {//method
    //records
    fetchRecords(state) {
      // const x: RecordItem[] =JSON.parse(window.localStorage.getItem('recordList') || '[]');
      // return x;
      //
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[];
      
  },

    createRecord(state,record){
    // recordListModel.create(record)
    const record2: RecordItem = clone(record);
    record2.createdAt = new Date().toISOString();
    state.recordList.push(record2);//data 
    console.log(state.recordList)
    store.commit('saveRecords')
    window.alert('saved')
    },
    saveRecords(state){
      window.localStorage.setItem(localStorageKeyName, JSON.stringify(state.recordList));
    },
    //tags
    fetchTags(state){
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]') ;
      if(!state.tagList || state.tagList.length===0){
        // store.commit('createTag','')
        // store.commit('createTag','')
        // store.commit('createTag','')
        // store.commit('createTag','')
        // store.commit('createTag','')
        state.tagList.push({id: '1',name: ''});
        state.tagList.push({id: '2',name: ''});
        state.tagList.push({id: '3',name: ''});
        state.tagList.push({id: '4',name: ''});
        state.tagList.push({id: '5',name: ''});
        store.commit('saveTags')
      }
    } ,
    createTag(state,name: string) {

      //this.data = [{id:'1',name:'xxx'},{id:'1',name:'xxxxxx'}]
      const names = state.tagList.map(item=>item.name)
      //dataname
      
      if(names.indexOf(name)>=0){ 
          window.alert('')
          return 'duplicated'}
      const id = createId().toString();//
      state.tagList.push({id: id,name: name});
      store.commit('saveTags')
      window.alert('');
      return 'success';
      
    },
    saveTags(state){
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    },
    findTag(state, id: string){
      return state.tagList.filter(t => t.id===id)[0];
    },
    setCurrentTag(state,id: string){
      
      state.currentTag =  state.tagList.filter(t => t.id===id)[0];
      
    },
    updateTag (state,payload: {id: string;name: string}){
      const{id,name}=payload
      const idList = state.tagList.map(item => item.id)
          if(idList.indexOf(id)>=0){
              const names =state.tagList.map(item => item.name)
              if(names.indexOf(name)>=0){
                  window.alert('')
                  return 'duplicated'
              }else{
                  const tag = state.tagList.filter(item => item.id ===id)[0]
                  tag.name=name;
                  tag.id=id;
                  // this.saveTags()
                  store.commit('saveTags')
                  // return "success"
              }
          }else{
              // return 'not-found'
          }
    },

    removeTag(state,id: string) {
      // if(tagListModel.remove(id)){
      //   return true;
      //   // this.$router.replace('/labels')
      // }else{
      //   // window.alert('')
      //   return false
      // }
      // return tagListModel.remove(id)
      let index  = -1;
          for(let i= 0 ;i<state.tagList.length; i++ ){
              if(state.tagList[i].id ===id){
                  index = i
                  break;
              }
              
          }
          if(index>=0){state.tagList.splice(index,1);
            store.commit('saveTags');
            console.log('done')
            window.alert('')
            router.replace('/labels')
          }else{
            window.alert('')
          }
          
          
          
    },
    
  },
  // actions: {
  // },
  // modules: {
  // }
})


export default store;